'use strict';

var log = require('./Log.js').Logger('libZotero:Client');

var Client = function(apiKey=''){
	this._apiKey = apiKey;
	this.net = require('./Net.js');
};

Client.prototype.getUserGroups = function(userID) {
	var aparams = {
		'target':'userGroups',
		'libraryType':'user',
		'libraryID': userID,
		'order':'title'
	};
	
	if(this._apiKey){
		aparams['key'] = this._apiKey;
	}
	
	return Zotero.ajaxRequest(aparams)
	.then(function(response){
		log.debug('fetchUserGroups proxied callback', 3);
		let groupJson = response.data;
		let groups = groupJson.map(function(groupObj){
			return new Zotero.Group(groupObj);
		});
		
		var fetchedGroups = groups.addGroupsFromJson(response.data);
		response.fetchedGroups = fetchedGroups;
		return response;
	});
};

Client.prototype.getUserPublications = function(userID, config={}) {
	log.debug('Zotero.Client.loadPublications', 3);
	
	var defaultConfig = {
		target:'publications',
		start: 0,
		limit: 50,
		order: Zotero.config.defaultSortColumn,
		sort: Zotero.config.defaultSortOrder,
		include: 'bib'
	};
	
	//Build config object that should be displayed next and compare to currently displayed
	var newConfig = Z.extend({}, defaultConfig, config);
	
	var urlconfig = Z.extend({
		'target':'publications',
		'libraryType':'user',
		'libraryID':userID
	}, newConfig);
	
	let publicationItems = [];
	let fetcher = new MultiFetch(urlconfig);

	return Zotero.ajaxRequest(urlconfig)
	.then(function(response){
		log.debug('loadPublications proxied callback', 3);
		var publicationItems = [];
		var parsedItemJson = response.data;
		parsedItemJson.forEach(function(itemObj){
			var item = new Zotero.Item(itemObj);
			publicationItems.push(item);
		});
		
		response.publicationItems = publicationItems;
		return response;
	});

};

Client.prototype.getKeyPermissions = function(key=false) {
	if(!key){
		return false;
	}
	
	var urlconfig = {'target':'key', 'apiKey':key, 'libraryType':''};
	
	return Zotero.ajaxRequest(urlconfig)
	.then(function(response){
		var keyObject = JSON.parse(response.data);
		return keyObject;
	});
};

Client.prototype.deleteKey = function(key=false) {
	if(!key){
		return false;
	}

	return this.net.ajax();
};

var MultiFetch = function(config={}){
	let defaultConfig = {
		start: 0,
		limit: 50
	};
	
	this.config = Z.extend({}, defaultConfig, config);
	this.hasMore = true;
};

MultiFetch.prototype.next = function(){
	if(this.hasMore == false){
		return Promise.resolve(null);
	}
	
	let nconfig = Z.extend({}, this.config);
	let p = Zotero.ajaxRequest(nconfig);
	p.then((response)=>{
		if(response.parsedLinks.hasOwnProperty('next')){
			this.hasMore = true;
		} else {
			this.hasMore = false;
		}

		return response;
	});

	nconfig.start = nconfig.start + nconfig.limit;
	return p;
};

MultiFetch.prototype.fetchAll = function(){
	let results = [];
	let tryNext = () => {
		if(this.hasMore){
			return this.next().then((response)=>{
				results = results.concat(response.data);
			}).then(tryNext);
		} else {
			return Promise.resolve(results);
		}
	};

	return tryNext();
};



module.exports = Client;
